import React, { useRef, useState } from 'react'
import {createUserWithEmailAndPassword, signInWithEmailAndPassword, signOut} from "firebase/auth"
import { set, ref as dbRef } from 'firebase/database'
// if there is a saved account show logout
// if there is not show create account
// show option to login instead (which will change a state variable)
// login button will try to login, if no input switch state

function Auth(props) {

    const [errorMessage, setErrorMessage] = useState()

    const emailInput = useRef()
    const passInput = useRef()
    const passInput2 = useRef()

    function createUser(){

        // Get the inpu values
        const emailInputValue = emailInput.current.value
        const passInputValue = passInput.current.value
        const passInputValue2 = passInput2.current.value

        // Verify that the user input an email and a password
        if(!emailInputValue){
            showError("please input an email address")
            return
        }
        if(!passInputValue){
            showError("please input a password")
            return
        }
        // If the input passwords do not match inform the user to try again
        if(!(passInputValue === passInputValue2)){
            showError("passwords do not match, please try again")
            return
        }

         // Use input values to create a user
        createUserWithEmailAndPassword(props.firebase.current.auth, emailInputValue, passInputValue).then((userCredential)=>{
            
            console.log("created user with id: " + userCredential.user.uid)
            
            // copy the input form data into their account somehow
            set(dbRef(props.firebase.current.database, "users/"+userCredential.user.uid+"/admin"), false)
        }).catch(error => {
            // If there is an error show the user
            showError(error)
        })

    }
    function signInUser(){
        
        // Get the inpu values
        const emailInputValue = emailInput.current.value
        const passInputValue = passInput.current.value        

        if(!emailInputValue){
            showError("please input an email address")
            return
        }
        if(!passInputValue){
            showError("please input a password")
            return
        }

        // Use them to log in
        signInWithEmailAndPassword(props.firebase.current.auth, emailInputValue, passInputValue).then(user => {
            // call a function that puts the input data into the users account
        }).catch(error => {            
            // If there is an error show the user
            showError(error.code)
        })

    }

    function showError(_errorString){
        setErrorMessage(_errorString)
    }

  return (
    <div className='authWindow'>
        <div className='closeButton' onClick={()=>props.setShowAuth(false)}>x</div>
        {props.userId ?
            <div className='loggedInAuthWindow'>
                {props.isAdmin && <button onClick={()=>props.setPage("Admin")}>View All</button>}
                <button>View Your Forms</button>
                <button onClick={()=>props.setPage("Input")}>New / Resume Form</button>
                <button>Reset Password</button>
                <button>Change Email</button>
                <button onClick={()=>signOut(props.firebase.current.auth).then(()=>{}).catch(()=>{})}>Log Out</button>
            </div>
        :
            <div>
                <input placeholder='email' ref={emailInput}></input>
                <input placeholder='password' type={"password"} ref={passInput}></input>
                <input placeholder='password confirmation' type={"password"} ref={passInput2}></input>
                <div className='errorDisplay'>
                    {errorMessage}
                </div>
                <div className='bottomButtons'>
                    <button onClick={signInUser}>Sign In</button>
                    <button onClick={createUser}>Create Account</button>
                </div>
            </div>            
        }
    </div>
  )
}

export default Auth