import { getDownloadURL, uploadBytes, ref as storageRef } from 'firebase/storage'
import {push, set, ref as dbRef} from "firebase/database"

import React, { useEffect, useRef, useState } from 'react'
import newImageIcon from "../Images/newImageIconScaledPadding.png"
import NewSubmissionConfirmation from '../Components/NewSubmissionConfirmation'

function InputForm(props) {
    
    const [imageFileUrl, setImageFileUrl] = useState()
    const [inputObject, setInputObject] = useState()

    const imageFileRef = useRef()

    const [newFormConfirmationBox, setNewFormConfirmationBox] = useState()

    const typeTimeoutRef = useRef()
    const uploadKey = useRef()
    const newImage = useRef(false)
    
    const nameInput = useRef()
    const phoneInput = useRef()
    const ageInput = useRef()
    const colorInput = useRef()
    const locationInput = useRef()
    const answerInput1 = useRef()
    const answerInput2 = useRef()
    const answerInput3 = useRef()
    const imageUrlRef = useRef(null)
    const imageName = useRef()
    const pressedSubmit = useRef()

    useEffect(()=>{
        uploadKey.current = localStorage.getItem("uploadKey")
        console.log("loaded previous responses")        
        const loadedInputObject = JSON.parse(localStorage.getItem("inputObject"))
        setInputObject(loadedInputObject)
        if(loadedInputObject)
            imageUrlRef.current = loadedInputObject.imageUrl
        console.log(loadedInputObject)
    },[])

    // When an image file is selected save and display it 
    function imageFileSelection(event){
        // Get the file from the event
        var selectedFile = event.target.files[0]  
        // Need to save in a ref because the setTimeout can't see the current value of the state variable
        imageFileRef.current = selectedFile   
        // Create a local url and put it in state so its displayed
        setImageFileUrl(URL.createObjectURL(selectedFile))
        // Call this function so it will auto upload after the image is selected
        typed()
        // There is a new image to be uploaded so set the flag var
        newImage.current = true
    }

    // Upload the input data
    function uploadForm(){
        console.log("called upload")

        console.log("uploadKey.current: " + uploadKey.current)
        console.log("!uploadKey.current: "+uploadKey.current)
        console.log(uploadKey)
        // If no key push to db to create one and save it
        if(!uploadKey.current || uploadKey.current == null){
            console.log("no key, creating one")
        
            // Push to db to create a key                                    
            const newRef = push(dbRef(props.firebase.current.database))

            // Save key in ref and localStorage
            localStorage.setItem("uploadKey", newRef.key)
            uploadKey.current = newRef.key

            // Call this function again but with the key saved. If there is some kind of error creating the key watch out for infinite loops            
            uploadForm()

        }
        // If there is a key upload the data
        else{
            console.log("there is a key, uploading data")

            // If there is an image that needs to be uploaded
            if(newImage.current){
                console.log("uploading a new image")

                console.log(imageFileRef.current)
                console.log(imageFileRef.current.name)

                // Upload the image
                uploadBytes(storageRef(props.firebase.current.storage, "/images/" + uploadKey.current + " " + imageFileRef.current.name), imageFileRef.current).then(uploadSnapshot => {
                    // Get the downlaod URL and save it
                    getDownloadURL(uploadSnapshot.ref).then(downloadUrl => {
                        
                        // Save the url in a ref
                        imageUrlRef.current = downloadUrl
                        imageName.current = imageFileRef.current.name

                        // Set the flag var and call this function again
                        newImage.current = false

                        // Call this function again with the new url and flag set
                        uploadForm()
                    })
                })
            }
            else{
                // Upload input data
                console.log("uploading input data")

                // Create a date object so the modified date and time can be saved
                var date = new Date()

                // Create an object based on the input data
                const newInputObject = {
                    name: nameInput.current.value,
                    phone: phoneInput.current.value,
                    age: ageInput.current.value,
                    color: colorInput.current.value,
                    location: locationInput.current.value,
                    answer1: answerInput1.current.value,
                    answer2: answerInput2.current.value,
                    answer3: answerInput3.current.value,         
                    // If these properties are already saved in the input object use them, else use new values          
                    imageUrl: imageUrl(),
                    imageName: ((inputObject && inputObject.imageName) || imageName.current) || "No Image",
                    // If there is an input object use the startedDate in there, else use the current date
                    startedDate: (inputObject && inputObject.startedDate) || (date.getDay() + "-" + date.getMonth() + "-" + date.getFullYear() + " " + date.getHours + ":" + date.getMinutes),
                    modifiedDate: date.getDay() + "-" + date.getMonth() + "-" + date.getFullYear() + " " + date.getHours + ":" + date.getMinutes
                }
                
                // Save the input object in state so the startedDate stays the same even when the user keeps editing it
                setInputObject(newInputObject)

                console.log("attempting to save user input:")
                console.log(newInputObject)

                // Put the info in the db as an object
                set(dbRef(props.firebase.current.database, "submissions/"+uploadKey.current), newInputObject)
                // Put the info in local storage so it can be retrieved if the user has multiple sessions
                localStorage.setItem("inputObject", JSON.stringify(newInputObject))

                console.log("saved user input:")
                console.log(newInputObject)

                if(pressedSubmit.current)
                    props.togglePage()
            }
        }
    }

    function imageUrl(){
        // If there is a new image put that link in the db
        if(imageUrlRef.current){
            console.log("there is a new imaeg returning "+imageUrlRef.current)
            return imageUrlRef.current
        }
        // If there is no new image and there is already an image in an input object use that
        else if(inputObject && inputObject.imageUrl){
            console.log("there no new image and one exists. returning "+inputObject.imageUr)
            return inputObject.imageUrl
        }
        // If there is no new or saved image return null
        else{
            console.log("there are no new or saved images")
            return null
        }
    }

    // Whever the user types this will be called. It will cause upload after 2 seconds of inactivity after typing
    function typed(){
        // cancel any previous time out
        clearTimeout(typeTimeoutRef.current)
        
        // setTimeout for 2 seconds
        typeTimeoutRef.current = setTimeout(() => {
            uploadForm()    
        }, 2000);        
    }

    function submitForm(){

        // This will switch out of the page, needs to happen after the file is uploaded
        //props.togglePage()
        // So set a flag variable and toggle the page in the uploadForm function
        pressedSubmit.current = true

        // Call this function to ensure the form uploads before the page switches
        uploadForm()        
        
    }

    function newSubmission(){

        // Reset the localStorage
        localStorage.removeItem("uploadKey")
        localStorage.removeItem("inputObject")
        // This should not be necessary
        setInputObject(null)
        setImageFileUrl(null)
        imageFileRef.current = null
        uploadKey.current = null
        newImage.current = false
        //setNewFormConfirmationBox(false)    

        // Refresh the page
        window.location.reload()
    }

  return (
    
    <div className='form'>
        {newFormConfirmationBox &&
            <NewSubmissionConfirmation 
                setNewFormConfirmationBox={setNewFormConfirmationBox} 
                newSubmission={newSubmission}
            ></NewSubmissionConfirmation>
        }
        <div className='topBox'>
            <label className='imageDiv' for={"imageInput"}>    
   
                {imageFileUrl || (inputObject && inputObject.imageUrl)
                    ?
                    <img src={imageFileUrl || (inputObject && inputObject.imageUrl)}></img>                
                    :
                    <img src={newImageIcon}></img>
                }
                <input id={"imageInput"} type={"file"} className={"hidden"} onChange={event=>imageFileSelection(event)}></input>
            </label>
            <div className='inputBox'>
                <div className='title'>Input Form</div>
                <input placeholder='name' onChange={typed} ref={nameInput} defaultValue={inputObject && inputObject.name}></input>
                <input placeholder='phone' onChange={typed} ref={phoneInput} defaultValue={inputObject && inputObject.phone}></input>
                <input placeholder='age' onChange={typed} ref={ageInput} defaultValue={inputObject && inputObject.age}></input>
                <input placeholder='favorite colors' onChange={typed} ref={colorInput} defaultValue={inputObject && inputObject.color}></input>
                <input placeholder='locations' onChange={typed} ref={locationInput} defaultValue={inputObject && inputObject.location}></input>
            </div>
        </div>
        <textarea placeholder='What are your thoughts about a?' onChange={typed} ref={answerInput1} defaultValue={inputObject && inputObject.answer1}></textarea>
        <textarea placeholder='What are your thoughts about b?' onChange={typed} ref={answerInput2} defaultValue={inputObject && inputObject.answer2}></textarea>
        <textarea placeholder='Why are you filling out this form?' onChange={typed} ref={answerInput3} defaultValue={inputObject && inputObject.answer3}></textarea>
        <div className='bottomButtons'>
            <button onClick={()=>setNewFormConfirmationBox(true)}>New Submission</button>
            <button onClick={submitForm}>Submit</button>
        </div>
    </div>
  )
}

export default InputForm