import React, { useEffect, useRef, useState } from 'react'
import {onValue, ref as dbRef} from "firebase/database"
import TileView from '../Components/TileView'

function Admin(props) {

  const setup = useRef()

  const [dataArray, setDataArray] = useState([])
  const [viewTile, setViewTile] = useState()
  const [selectedTile, setSelectedTile] = useState()

  useEffect(()=>{
    if(!setup.current){
      getData()
      setup.current = true
    }
  },[])

  // Get the user submissions
  function getData(){
    onValue(dbRef(props.firebase.current.database, "submissions"), snap => {
      //Put them in an array
      var tempArray = []
      snap.forEach(snapChild => {
        tempArray.push(snapChild.val())
      })
      // Put the data array in state to be displayed
      setDataArray(tempArray)
    })
  }

  function openTile(_tile){
    setSelectedTile(_tile)
    setViewTile(true)
  }

  return (
    <div>
      {dataArray.map( item =>(
        <div className='submissionTile' onClick={()=>openTile(item)}>
          <img src={item.imageUrl}></img>
          <div className='submissionTileInfo'>
            {item.name}
          </div>
        </div>
      ))}
      {viewTile && <TileView selectedTile={selectedTile} setViewTile={setViewTile}></TileView>}
    </div>
  )
}

export default Admin