import React from 'react'

function TileView(props) {
  return (
    <div className='tileView lightTheme'>
        <div className='closeButton' onClick={()=>props.setViewTile(false)}>x</div>
        
        <div className='tileViewImage'>
            <img src={props.selectedTile && props.selectedTile.imageUrl}></img>
        </div>
        
        <div className='tileViewData'>
            <div className='tileViewLine'>
                {props.selectedTile && props.selectedTile.name}
            </div>
            <div className='tileViewLine'>
                {props.selectedTile && props.selectedTile.phone}
            </div>
            <div className='tileViewLine'>
                {props.selectedTile && props.selectedTile.color}
            </div>
            <div className='tileViewLine'>
                {props.selectedTile && props.selectedTile.location}
            </div>
            <div className='tileViewLine'>
                {props.selectedTile && props.selectedTile.answer1}
            </div>
            <div className='tileViewLine'>
                {props.selectedTile && props.selectedTile.answer2}
            </div>
            <div className='tileViewLine'>
                {props.selectedTile && props.selectedTile.answer3}
            </div>
        </div>
    </div>
  )
}

export default TileView