import React from 'react'

function NewSubmissionConfirmation(props) {
  return (
    <div className='confirmationBox'>
        <div className='closeButton' onClick={()=>props.setNewFormConfirmationBox(false)}>x</div>
        <div className='messageText'>
            Are you sure you want to start over with a new submission? The input fields will all be reset.
        </div>
        <div className='bottomButtons bottom'>
            
            <button onClick={()=>props.setNewFormConfirmationBox(false)}>Cancel</button>
            <button onClick={()=>props.newSubmission()}>Start New</button>
        </div>
    </div>
  )
}

export default NewSubmissionConfirmation